@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
:root{--primary: #2175f5}*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins", sans-serif}body{background-color:#efefef}.container{display:flex;align-items:center;justify-content:center;min-height:100vh;overflow:hidden;padding:40px}.card{display:flex;align-items:center;justify-content:center;width:860px}.card .nike{color:white;font-size:11rem;left:15px;line-height:0.9;opacity:0.1;position:absolute;text-transform:uppercase;top:85px}.shoeBackground{position:relative;width:50%;height:475px;box-shadow:-15px 0 35px rgba(0,0,0,0.1),0 -15px 35px rgba(0,0,0,0.1),0 15px 35px rgba(0,0,0,0.1);transition:0.5s}.gradients{position:absolute;width:100%;height:100%;left:0;top:0}.gradient{position:absolute;width:100%;height:100%;left:0;top:0;z-index:-2}.gradient[color="blue"]{background-image:linear-gradient(45deg, #0136af, #22abfa)}.gradient[color="red"]{background-image:linear-gradient(45deg, #d62926, #ee625f)}.gradient[color="green"]{background-image:linear-gradient(45deg, #11998e, #1ce669)}.gradient[color="orange"]{background-image:linear-gradient(45deg, #fc4a1a, #f7b733)}.gradient[color="black"]{background-image:linear-gradient(45deg, #111, #666)}.behind{z-index:-1 !important}.display{z-index:0;-webkit-animation:0.8s width ease;animation:0.8s width ease}@-webkit-keyframes width{from{width:0%}to{width:100%}}@keyframes width{from{width:0%}to{width:100%}}.logo{position:absolute;width:100px;left:20px;top:20px}.share{display:flex;align-items:center;justify-content:center;background:#fff;border-radius:50%;color:#2175f5;color:var(--primary);font-size:1.3rem;height:50px;position:absolute;right:15px;text-decoration:none;top:15px;transition:0.5s;width:50px;z-index:10}.share:hover{transform:scale(1.1)}.shoe{bottom:0;opacity:0;position:absolute;right:0;transform:rotate(-20deg);width:140%}.shoe.show{opacity:1}.info{background:#fff;box-shadow:15px 0 35px rgba(0,0,0,0.1),0 -15px 35px rgba(0,0,0,0.1),0 15px 35px rgba(0,0,0,0.1);width:50%;padding:35px 40px;z-index:1}.info .shoeName,.info .description,.info .color-container,.info .size-container{border-bottom:1px solid #dadada}.info .shoeName{padding:0 0 10px 0}.info .shoeName div{display:flex;align-items:center}.info .shoeName div .big{color:#333;font-size:2rem;margin-right:10px;line-height:1}.info .shoeName div .new{background:#2175f5;background:var(--primary);border-radius:5px;color:#fff;padding:3px 10px;text-transform:uppercase}.info .shoeName .small{color:#444;font-weight:500;margin-top:3px;text-transform:capitalize}.info .description{padding:10px 0}.info .title{color:#3a3a3a;font-weight:600;font-size:1.2rem;text-transform:uppercase}.info .text{color:#555;font-size:17px}.info .color-container{padding:10px 0}.info .colors{display:flex;align-items:center;padding:8px 0}.info .color{cursor:pointer;border:5px solid;border-radius:50%;height:25px;margin:0 10px;transition:0.5s;width:25px}.info .color[color="blue"]{background-color:#2175f5;border-color:#2175f5}.info .color[color="red"]{background-color:#f84848;border-color:#f84848}.info .color[color="green"]{background-color:#29b864;border-color:#29b864}.info .color[color="orange"]{background-color:#ff5521;border-color:#ff5521}.info .color[color="black"]{background-color:#444;border-color:#444}.info .color.active{border-color:#fff;box-shadow:0 0 10px 0.5px rgba(0,0,0,0.2);transform:scale(1.1)}.info .size-container{padding:10px 0;margin-bottom:10px}.info .size-container .sizes{display:flex;align-items:center;padding:8px 0}.info .size-container .sizes .size{display:flex;align-items:center;justify-content:center;cursor:pointer;background:#eee;border-radius:6px;font-weight:500;font-size:1.1rem;height:40px;margin:0 10px;width:40px}.info .size-container .sizes .size.active{background:#2175f5;background:var(--primary);color:white}.info .buy-price{display:flex;align-items:center;padding:15px 0;justify-content:space-between}.info .buy-price .buy{background:#2175f5;background:var(--primary);color:white;font-size:1.1rem;font-weight:500;padding:0.7rem 1rem;text-decoration:none;text-transform:uppercase;transition:0.5s}.info .buy-price .buy:hover{transform:scale(1.1)}.info .buy-price .buy svg{margin-right:5px}.info .buy-price .price{align-items:flex-start;color:#333;display:flex}.info .buy-price .price h1{font-size:2.1rem;font-weight:600;line-height:1}.info .buy-price .price svg{font-size:1.4rem;margin-right:1px}@media (max-width: 1070px){.shoe{width:135%}}@media (max-width: 1000px){.github-fork-ribbon{display:none}.card{flex-direction:column;width:100%;box-shadow:0 0 35px 1px rgba(0,0,0,0.2)}.card>div{width:100%;box-shadow:none}.card .shoe{width:100%;transform:rotate(-5deg) translateY(-50%);top:55%;right:2%}.card .nike{top:20%;left:5%}}@media (max-width: 600px){.share{width:40px;height:40px}.share svg{font-size:1rem}.logo{width:70px}}@media (max-width: 490px){.card .nike{font-size:7rem}.info{padding:20px}.info .big{font-size:1.3rem !important}.info .small{font-size:1rem}.info .new{padding:2px 6px;font-size:0.9rem}.info .title{font-size:0.9rem}.info .text{font-size:0.8rem}.info .buy-price .buy{padding:0.5rem 0.8rem;font-size:0.8rem}.info .buy-price .price h1{font-size:1.5rem}.info .buy-price .price svg{font-size:0.9rem}.info .color-container .colors .color{margin:0 20px;width:20px;height:20px;border-width:4px}.info .size-container .sizes .size{width:30px;height:30px;margin:0 8px;font-size:0.9rem;line-height:30px}.share{width:35px;height:35px;top:10px;right:10px}svg{font-size:0.8rem}.logo{width:60px;top:10px;left:10px}}@media (max-width: 400px){.buy svg{display:none}.container{padding:10px}}@media (max-width: 340px){.info .color-container .colors{display:flex;justify-content:space-around}.info .color-container .colors .color{margin:0}.info .size-container .sizes{display:flex;justify-content:space-around}.info .size-container .sizes .size{margin:0}}

